.type {
  &__block {
    display: flex;
    flex-wrap: wrap;
    column-gap: 25px;
    row-gap: 15px;
    margin-top: 31px;
  }

  &__title {
    font-size: 17px;
    line-height: 18px;
    color: var(--black);
    font-weight: 700;
    margin-left: 19px;
    width: 200px;
  }

  &__label {
    display: flex;
    align-items: center;
  }

  &__input {
    appearance: none;
  }

  &__control-mark {
    position: relative;
    display: block;
    box-sizing: border-box;
    width: 42px;
    flex: none;
    height: 42px;
    background: var(--white);
    border: 2px solid rgb(0 0 0);
    border-radius: 50%;
  }
}
